<template>
    <div class="page">
        <div
            ref="main"
            :class="{
                main: true,
                promoter: this.pageName === 'promoter',
                partner: this.pageName === 'partner',
            }"
        >
            <div
                class="btn scale-infinite"
                @click="goTarget" />
        </div>
    </div>
</template>

<script>
export default {
    name: 'Recruit',
    mounted() {
        this.$root.hidePlaceholder();
        this.$o2o({
            e_c: '页面曝光',
            e_a: '曝光',
            e_n: this.pageInfo.name,
        });

        const height = (window.innerWidth / 360) * 640;
        this.$refs.main.style.height = `${height}px`;
    },
    computed: {
        pageName() {
            const name = this.$route.query.name || 'promoter';

            return name;
        },
        pageInfo() {
            const info = {
                promoter: {
                    name: '想看地推大使招募',
                },
                partner: {
                    name: '想看合伙人招募',
                },
            };

            return info[this.pageName];
        },
    },
    methods: {
        goTarget() {
            const callback = () => {
                const target = this.$route.query.target || 'https://jinshuju.net/f/F1KurV';
                const url = `bikan://web.goto/commonWebView?url_encode=${encodeURIComponent(target)}&show_title=false&page_title=问卷星&menu=share&start_source=recruit&full_screen=false`;

                location.href = url;
            };

            this.$o2o({
                e_c: '按钮点击',
                e_a: '点击',
                e_n: this.pageInfo.name,
            }, callback, callback);
        },
    },
};
</script>

