}

.btn {
    height: 74px;
    width: 100%;
    position: absolute;
    bottom: 0;
    left: 0;
    background-size: 95% 74px;
    background-repeat: no-repeat;
    background-position: 50% 50%;
}

.partner .btn {
    background-image: url(https://ts.market.mi-img.com/download/Browser/00dbf48f7c3d357ef0dc394f9a3ffb62a0940bb12/partner-btn.png);
}

